main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 26px;
    background-color: #222;
    font-family: 'Arial';
}

div {
    color: #fff;
}

img {
    width: 70%;
}

.input_contaimer {
    width: 100%;
}

input {
    width: 100%;
    height: 56px;
    padding: 0 16px;
    color: #fff;
    background-color: #444;
    border: none;
    border-radius: 6px;
    outline: none;
}

.message {
    position: absolute;
    margin-left: 6px;
    font-size: 10px;
    line-height: 18px;
    color: rgb(185, 164, 164);
}

span {
    margin-top: 30px;
    text-align: center;
    color: #fff;
    font-size: 14px;
}

.town {
    margin: 10px 0;
    color: #D4AF37;
}

button {
    height: 48px;
    margin-top: 35px;
    padding: 0 26px;
    border: 4px solid #D4AF37;
    border-radius: 28px;
    color: #fff;
    outline: none;
    background: transparent;
}

button.disabled {
    opacity: 0.5;
}

.auto_complite {
    position: relative;
    width: 90%;
    z-index: 10;
}

.list {
    position: absolute;
    top: 6px;
    width: 100%;
    min-height: 150px;
    background-color: #444;
}

.list.mid {
    display: flex;
    justify-content: center;
    align-items: center;
}

.list > div:not([aria-busy="true"]) {
    height: 32px;
    padding: 0 12px;
    line-height: 32px;
    font-size: 12px;

    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.list > div:not([aria-busy="true"]):hover {
    background-color: #5A5A5A;
}